import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import './Header.scss';

const LinkList:React.FC<{isMenu: boolean, toggle: any}> = ({isMenu, toggle}) => {
  const [nightIsMode, setNightIsMode] = useState(false);
  const toggleNightIsMode = () => setNightIsMode(!nightIsMode);

  if(nightIsMode) {
    document.querySelector("body")?.classList.add("night");
  } else {
    document.querySelector("body")?.classList.remove("night");
  };

  return (
    <ul className={`navbar-nav me-auto link-list ${isMenu && "hide"}`}>
      <li className="nav-item">
        <NavLink 
          activeStyle={{
            fontWeight: "bold",
            color: "rgb(255, 255, 255)",
            fontSize: "22px",
          }} 
          style={{
            fontWeight: "bold",
            color: "black",
            textDecoration: "none"
          }}
          to="/main"
          onClick={() => toggle()}
        >
          main
        </NavLink>
      </li>
      <li className="nav-item">
        <NavLink 
          activeStyle={{
            fontWeight: "bold",
            color: "rgb(255, 255, 255)",
            fontSize: "22px",
          }} 
          style={{
            fontWeight: "bold",
            color: "black",
            textDecoration: "none"
          }}
          to="/tests"
          onClick={() => toggle()}
        >
          tests
        </NavLink>
      </li>
      <li className="nav-item">
        <NavLink 
          activeStyle={{
            fontWeight: "bold",
            color: "rgb(255, 255, 255)",
            fontSize: "22px",
          }} 
          style={{
            fontWeight: "bold",
            color: "black",
            textDecoration: "none"
          }}
          to="/story"
          onClick={() => toggle()}
        >
          story
        </NavLink>
      </li>
      <li className="nav-item">
        <NavLink 
          activeStyle={{
            fontWeight: "bold",
            color: "rgb(255, 255, 255)",
            fontSize: "22px",
          }} 
          style={{
            fontWeight: "bold",
            color: "black",
            textDecoration: "none"
          }}
          to="/sign-in"
          onClick={() => toggle()}
        >
          sign in
        </NavLink>
      </li>
      <li className="nav-item">
        <NavLink 
          activeStyle={{
            fontWeight: "bold",
            color: "rgb(255, 255, 255)",
            fontSize: "22px",
          }} 
          style={{
            fontWeight: "bold",
            color: "black",
            textDecoration: "none"
          }}
          to="/profile/setting"
          onClick={() => toggle()}
        >
          profile
        </NavLink>
      </li>
      <li className="nav-item">
        <label className="container-checkbox">
          <input type="checkbox" id="night-mode" name="night-mode" value="todo" className="top-10" onClick={() => toggleNightIsMode()}/>
        </label>
      </li>
    </ul>
  )
}

const Header: React.FC = () => {
  const [isMobileMenu, setIsMobileMenu] = useState(false);
  const toggleMobileMenu = () => setIsMobileMenu(!isMobileMenu);

  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark header-fixed">
      <div>
        <button onClick={() => setIsMobileMenu(!isMobileMenu)} className="navbar-toggler toggle-mobile" type="button" data-bs-toggle="collapse" data-bs-target="#navbarColor02" aria-controls="navbarColor02" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon btn-menu"></span>
        </button>

        <LinkList isMenu={isMobileMenu} toggle={toggleMobileMenu}/>
      </div>
  </nav>
  )
};

export default Header;
