import React from 'react';
import { Nav, NavItem } from 'reactstrap';
import classnames from 'classnames';
import { useHistory, useLocation } from 'react-router';
import './Tabs.scss';
import TabsContent from './TabsContent';

const Tabs: React.FC = () => {
  const location = useLocation();
  const history = useHistory();

  const list = (
    <Nav tabs className="tabs">
        <NavItem>
        <button
            className={`btn ${classnames({ active: location.pathname === "/profile/setting" })}`}
            onClick={() => { 
              history.push("/profile/setting");
            }}
          >
            <i className="bi bi-gear"></i>
            <span>setting</span>
          </button>
        </NavItem>
        <NavItem>
          <button
            className={`btn ${classnames({ active: location.pathname === "/profile/dictionary" })}`}
            onClick={() => { 
              history.push("/profile/dictionary");
            }}
          >
            <i className="bi bi-book-fill"></i>
            <span>dictionary</span>
          </button>
        </NavItem>
        <NavItem>
          <button
            className={`btn ${classnames({ active: location.pathname === "/profile/results" })}`}
            onClick={() => {
              history.push("/profile/results");
            }}
          >
            <i className="bi bi-card-checklist"></i>
            <span>results</span>
          </button>
        </NavItem>
        <NavItem>
          <button
            className={`btn ${classnames({ active: location.pathname === "/profile/all tests" })}`}
            onClick={() => { 
              history.push("/profile/tests");
            }}
          >
            <i className="bi bi-clipboard-check"></i>
            <span>tests</span>
          </button>
        </NavItem>
        <NavItem>
          <button
            className={`btn ${classnames({ active: location.pathname === "/profile/create test" })}`}
            onClick={() => { 
              history.push("/profile/create test");
            }}
          >
            
            <span>create test</span>
          </button>
        </NavItem>
      </Nav>
  )

  return (
    <div>
      { list }
      <TabsContent path={location.pathname} />
    </div>
  );
}

export default Tabs;